
#clock-app {
  font-family: 'Mukta', "Segoe UI", Arial, sans-serif;
  font-size: 5vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /*color: #2c3e50;*/
  /*padding-top: 60px;*/
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
}
#panel {
  height: 100%;
  background-color: black;
  color: white;
}
#menu {
  background-color: #e4e4e4;
  color: black;
  text-align: left;
  padding-left: 20px;
}
#menu a {
  color: inherit;
}
#menu .toggle-full-screen {
  margin-top: 6vh;
  text-decoration: underline;
}
.rpv-menu-button {
  font-size: 12vh;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 5vh;
}



